<template lang='pug' src='./Detail.pug'></template>

<script>
export default {
  name: 'ConservationDetails',
  data () {
    return {
      path: this.$route.params.id,
      conservationTitle: '',
      conservationScope: '',
      conservationUrl: '',
      conservationLink: '',
      // JS Dictionary - Associative Arrays in JavaScript
      dict: {
        'pb-marmara': 0,
        'yayakarsa': 1,
        'diver-safety-guardian': 2,
        'sualti-kultur-mirasinin-korunmasi': 3,
        'karstdive-2020': 4,
        'deniz-kiyi-plaj-temizligi': 5,
        'saros-sualti-muzesi': 6,
        'photogrammetry': 7
      },
      // Conservations JSON
      conservations: [
        {
          title: 'Project Baseline: Marmara',
          scope: 'Marmara blgesi boyunca su koullar ve denizanas saylarn takip ediyoruz. Bu sayede zaman ierisinde gidiat daha iyi grebileceiz. Saros krfezinde ve zmit krfezinde kurduumuz iki farkl istasyon verileri toplamaya balad bile. Sizde farkndalk sahibi bir dalc olarak bu projede bize destek olabilirsiniz.',
          url: 'https://www.youtube-nocookie.com/embed/33FXnjxEPVc',
          link: 'https://gueturkey.com/pb-marmara/'
        },
        {
          title: 'Ya Yakarsa',
          scope: 'Deniz anas art son zamanlarda iklim ve besin a deiiklikliinden dolay artyor. Yurtta bilimine destek olarak siz de bu projeye destek olabilirsiniz.',
          link: 'https://yayakarsa.org'
        },
        {
          title: 'DAN Dal Ba',
          scope: 'Dal sonras dalclarda oluan sessiz kabarcklarn doppler lm ile dinlendikten sonra, insan vcudu zerindeki etkilerinin incelenmesi iin ortak bir veritabannda toplanmasna ynelik yaplan bir yurtta bilimidir.',
          url: 'https://www.youtube-nocookie.com/embed/iFxiGp8gNIY',
          link: 'https://www.diversafetyguardian.org'
        },
        {
          title: 'Sualt Kltr Mirasnn Korunmas',
          scope: 'Tarihin ekillenmesine yardmc oluyoruz. Bulduumuz kltr miraslarn arkeologlar ile paylayoruz.',
          url: 'https://www.youtube-nocookie.com/embed/rYxcSH0rWzg',
          link: 'http://underwaterculturalheritage.net'
        },
        {
          title: 'KarstDive 2020',
          scope: '1998 ylnda JJ ve ekibinin 122 metreye kadar kefettii, Aysa Ktasnn en derin maaras olan Suluin Maarasnda aratrma dalalarna devam ediyoruz. Amacmz, deniz ile balantsn ve dibini bulabilmek.',
          url: 'https://www.youtube-nocookie.com/embed/NjX3O-RN3-8',
          link: ''
        },
        {
          title: 'Deniz, Ky ve Plaj Temizlii',
          link: 'https://www.wwf.org.tr/calismalarimiz/oceans/deniz_ky_temizlii/'
        },
        {
          title: 'Saros Sualt Mzesi',
          scope: '',
          url: 'https://www.youtube-nocookie.com/embed/5BS3LHkt9hY',
          link: 'http://www.estab.gov.tr/dunyanin-ilk-su-alti-tarih-muzesi-saros-korfezinde/'
        },
        {
          title: 'Photogrammetry Yntemi ile 3D Modelleme',
          scope: '',
          url: 'https://www.youtube-nocookie.com/embed/D0UCWftL2ZI',
          link: 'https://sketchfab.com/nevcanuludas/followers'
        }
      ]
    }
  },
  created () {
    var c = this.conservations[this.dict[this.path]]
    this.conservationTitle = c.title
    this.conservationScope = c.scope
    this.conservationUrl = c.url
    this.conservationLink = c.link
  }
}
</script>

<style lang='sass' src='./Detail.sass' scoped></style>



